button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 2px solid;
  border-color: black;
  background: transparent;
  color: black;
  font-family: $font_sans;
  @include font-size(1);
  line-height: 1;
  padding: .5em 1em;

  &:hover ,
  &:active,
  &:focus {
    background: white;
    color: black;
  }
}